<template>
    <div>
        <div class="canvasImg" ref="imageWrapper">
            <!--html-->
            <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <span></span>
                    <el-button style="float: right; padding: 3px 0" type="text"></el-button>
                </div>
                <div v-for="o in 4" :key="o" class="text item">
                    {{' ' + o }}
                </div>
                <img src="../assets/img-big.jpg" height="600" width="410"/>
            </el-card>


        </div>
        <img :src="dataURL" alt="" />
        <el-button @click="toImage">canvas</el-button>

        <div>------------</div>
        <div class="copyBox">
            sysAppId<span>{{sysAppIds}}</span>
            <el-button class="ml10" type="primary" size="medium"
                       v-clipboard:copy="sysAppIds"
                       v-clipboard:success="onCopy"
                       v-clipboard:error="onError"></el-button>
        </div>
        <el-button @click="backTop"></el-button>
    </div>
</template>

<script>
  import html2canvas from 'html2canvas'
  export default {
    name: '',
    data () {
      return {
        dataURL: '',
        sysAppIds: 'zjzjzz'
      }
    },
    methods: {
      backTop(){
        console.log(1);
//        let top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
//        console.log(top);
//
//        document.documentElement.scrollTop = 0;
//        console.dir(document.documentElement);
        let total = 100
        let top = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
        document.body.scrollTop = 0
        // Firefox
        document.documentElement.scrollTop = 0
        // Safari
        window.pageYOffset = 0

        console.log(top);
//        console.log(document.body);
      },
//      let opts = {
//        scale: scale, // scale 
//        canvas: canvas, // canvas
//        logging: false, //html2canvas
//        width: width, //dom    canvas
//        height: height, //canvas
//        windowsHeight: document.body.scrollHeight, //X
//        windowsWidth: document.body.scrollWidth, //Y
//        x: 0, //Y
//        y: window.pageYOffset, //
//        useCORS: true // 
//      };

  toImage() {
        html2canvas(this.$refs.imageWrapper,{
          backgroundColor: null,
          userCORS:true,//
        }).then((canvas) => {
          let dataURL = canvas.toDataURL("image/png");
          this.dataURL = dataURL;
        });
      },
      // 
      onCopy(e){
        console.log(e.text);
      },
      // 
      onError(e){
        alert("");
      },
    }
  }
</script>

<style scoped>
    .text {
        font-size: 14px;
    }

    .item {
        margin-bottom: 18px;
    }

    .clearfix:before,
    .clearfix:after {
        display: table;
        content: "";
    }
    .clearfix:after {
        clear: both
    }

    .box-card {
        width: 480px;
    }
    .canvasImg{
        width: 500px;
    }
</style>



